const env = process.env.NODE_ENV || 'development';

const SERVER_HOST_DEV = 'localhost';
const SERVER_PORT_DEV = 5000;

const SERVER_SOCKET_DEV = `${SERVER_HOST_DEV}:${SERVER_PORT_DEV}`;
const SERVER_SOCKET_PROD = 'just-do-it-0.herokuapp.com';

const CONSTANTS = {
  HTTP_BASE_URL:
    env === 'production'
      ? `https://${SERVER_SOCKET_PROD}`
      : `http://${SERVER_SOCKET_DEV}`,
  WS_BASE_URL:
    env === 'production'
      ? `wss://${SERVER_SOCKET_PROD}`
      : `ws://${SERVER_SOCKET_DEV}`,
};

export default CONSTANTS;
