import { useEffect } from 'react';
import * as yup from 'yup';
import { addDays } from 'date-fns';
import socket from '../../api/ws';
import styles from './TaskForm.module.scss';
import { Formik, Form, ErrorMessage, Field } from 'formik';

const TASK_VALIDATION_SCHEMA = yup.object({
  body: yup.string().min(1).max(64).required(),
  deadline: yup.date().min(new Date()).required(),
});

function TaskForm ({ tasks, setTasks, userName }) {
  useEffect(() => {
    function onCreate (payload) {
      setTasks(tasks => [...tasks, payload]);
    }

    socket.on('created', onCreate);
    return () => {
      socket.off('created', onCreate);
    };
  }, []);

  const initialValues = { body: '', deadline: '' };

  function submitHandler (values) {
    socket.emit('task:create', { ...values, authorName: userName });

    // resetForm();
  }

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={submitHandler}
      validationSchema={TASK_VALIDATION_SCHEMA}
    >
      <Form>
        <label>
          <span>Task *</span>
          <Field type='text' name='body' autoFocus />
          <ErrorMessage name='body' component='div' />
        </label>

        <label>
          <span>Deadline *</span>
          <Field type='date' name='deadline' />
          <ErrorMessage name='deadline' component='div' />
        </label>

        <button type='submit'>+</button>
      </Form>
    </Formik>
  );
}

export default TaskForm;
