import React, { useEffect } from 'react';
import socket from './../../api/ws';

function TasksListItem ({ task, tasks, setTasks, userName }) {
  const { isDone, body, deadline, authorName, implementerName } = task;

  useEffect(() => {
    function onUpdate (payload) {
      setTasks(tasks => {
        const newTasks = [...tasks];
        newTasks[newTasks.findIndex(t => t.id === payload.id)] = { ...payload };
        return newTasks;
      });
    }

    socket.on('updated', onUpdate);
    return () => {
      socket.off('updated', onUpdate);
    };
  }, []);

  function handleChange ({ target: { checked } }) {
    if (checked)
      socket.emit('task:update', {
        ...task,
        isDone: true,
        implementerName: userName,
      });
  }

  return (
    <li>
      <div>
        <input type='checkbox' checked={isDone} onChange={handleChange} />
        <span> {body}</span>
      </div>
      <div>
        <span>{authorName}</span> <span>{deadline}</span>
      </div>
      {isDone && <div>Good job! Thank you, {implementerName}!</div>}
    </li>
  );
}

export default TasksListItem;
