import { useEffect, useState } from 'react';
import { BounceLoader } from 'react-spinners';
import { VscAccount } from 'react-icons/vsc';
import TaskForm from './components/TaskForm/index';
import TasksList from './components/TasksList';
import CONSTANTS from './constants';
import styles from './App.module.scss';

function App () {
  const [userName, setUserName] = useState('Anonymous');
  const [tasks, setTasks] = useState([]);
  const [isFetching, setIsFetching] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    setUserName(prompt('Enter Your Name'));
  }, []);

  useEffect(() => {
    setIsFetching(true);
    fetch(`${CONSTANTS.HTTP_BASE_URL}/tasks`)
      .then(response => response.json())
      .then(tasks => setTasks(tasks))
      .catch(e => {
        setError(e);
      })
      .finally(() => setIsFetching(false));
  }, []);

  return (
    <>
      <header>
        <div>
          <h1>JustDoIt</h1>
          <span>
            <VscAccount /> Hello, {userName}
          </span>
        </div>
      </header>

      <main>
        <TaskForm tasks={tasks} setTasks={setTasks} userName={userName} />
        {isFetching && <BounceLoader />}
        {error && <div>Something went wrong...</div>}
        {!isFetching && !error && (
          <TasksList tasks={tasks} setTasks={setTasks} userName={userName} />
        )}
      </main>

      <footer>
        <p>Made By/For Volunteers</p>
      </footer>
    </>
  );
}

export default App;
